











.welcome .card{max-width:500px;margin:100px auto
}
.welcome .card h2{font-size:3em
}
.welcome .card .btn{margin:30px auto
}

.chat h2{font-size:2.6em;margin-bottom:40px
}
.chat span{font-size:1.4em
}
.chat .time{display:block;font-size:1em
}
.chat .content{word-break:break-all
}
.chat .messages{max-height:300px;overflow:auto
}
.chat .messages::-webkit-scrollbar{width:3px
}
.chat .messages::-webkit-scrollbar-track{background-color:#ddd
}
.chat .messages::-webkit-scrollbar-thumb{background-color:#999
}
